.ng2-c-conversation {
  border-bottom: 1px solid #f5f5f5;
  display: block;
  overflow: hidden;
  position: relative;
  transform: translateX(0); }
  .ng2-c-conversation::after {
    clear: both;
    content: "";
    display: table; }

.ng2-c-conversation--active {
  background: #f5f5f5; }

.ng2-c-conversation--unread::before {
  background: #6ec60f;
  border-bottom: 2px solid #6ec60f;
  content: "";
  display: block;
  height: 120px;
  left: 0;
  position: absolute;
  top: 0;
  transform: rotate(-45deg) translate(-12px, -105px);
  width: 120px;
  z-index: 1; }

.ng2-c-conversation__avatars {
  float: left;
  left: 8px;
  position: relative;
  top: 9px;
  z-index: 2; }

.ng2-c-conversation__summary {
  box-sizing: border-box;
  color: #9b9b9b;
  padding: 15px 10px 15px 60px;
  width: 100%; }

.ng2-c-conversation__header {
  display: table;
  font-size: 14px;
  line-height: 20px;
  table-layout: fixed;
  width: 100%; }

.ng2-c-conversation__authors {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
  display: table-cell;
  overflow: hidden; }

.ng2-c-conversation__time {
  display: table-cell;
  text-align: right;
  width: 90px; }

.ng2-c-conversation__subject {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
  box-sizing: border-box;
  color: #4a4a4a;
  font-size: 18px;
  margin: 5px 0;
  overflow: hidden;
  padding-right: 15px;
  position: relative;
  width: 100%; }
  @media (max-width: 767px) {
    .ng2-c-conversation__subject::after {
      bottom: 0;
      color: #28648a;
      content: "\e906";
      font-family: "delen";
      font-size: 10px;
      line-height: 24px;
      position: absolute;
      right: 0;
      text-align: right;
      top: 0;
      width: 15px; } }

.ng2-c-conversation__message {
  -webkit-box-orient: vertical;
  display: block;
  display: -webkit-box;
  height: 40px;
  hyphens: auto;
  -webkit-line-clamp: 2;
  line-height: 20px;
  max-height: 40px;
  overflow: hidden;
  word-break: break-word; }

.ng2-c-messaging-form {
  display: flex;
  flex-direction: column; }

.ng2-c-messaging-form__item--fill {
  align-items: stretch;
  display: flex;
  flex: 1;
  overflow: hidden; }
  .ng2-c-messaging-form__item--fill .ng2-c-form-item {
    align-items: stretch; }

/** BLOCK
************************************************************/
.ng2-c-messages {
  bottom: 0;
  display: flex;
  flex-direction: column;
  left: 0;
  position: absolute;
  right: 0;
  top: 0; }

/** ELEMENT
************************************************************/
.ng2-c-messages__wrapper {
  align-items: stretch;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow: hidden; }

.ng2-c-messages__footer {
  border-top: solid 1px #f5f5f5;
  flex-shrink: 0; }

.ng2-c-messages__list {
  display: flex;
  flex-direction: column-reverse;
  flex-grow: 1;
  list-style-type: none;
  overflow: auto; }

.ng2-c-message__item {
  align-items: flex-end;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  padding: 15px;
  padding-top: 0;
  position: relative; }
  .ng2-c-message__item:last-child {
    padding-top: 10px; }

.ng2-c-message__avatar {
  display: none; }

.ng2-c-message__summary {
  align-items: flex-end;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  flex-shrink: 0;
  max-width: 625px;
  width: 80%; }
  @media (min-width: 768px) {
    .ng2-c-message__summary {
      width: 70%; } }

.ng2-c-message__message {
  background-color: #cccccc;
  border-radius: 4px;
  color: #4a4a4a;
  font: inherit;
  hyphens: auto;
  line-height: 22px;
  padding: 15px;
  white-space: pre-wrap;
  word-break: break-word; }

.ng2-c-message__footer {
  color: #9b9b9b;
  line-height: 18px;
  padding: 10px 0; }
  .ng2-c-message__footer::after {
    clear: both;
    content: "";
    display: table; }

.ng2-c-message__author {
  display: none; }
  .ng2-c-message__author::after {
    content: ", ";
    display: inline-block; }

/** MODIFIER
************************************************************/
.ng2-c-messages--active {
  background-color: #ffffff; }

.ng2-c-message__item--grouped.ng2-c-message__item {
  padding-bottom: 5px; }

.ng2-c-message__item--grouped.ng2-c-message__item--incoming .ng2-c-message__avatar {
  display: none; }

.ng2-c-message__item--grouped .ng2-c-message__footer {
  display: none; }

.ng2-c-message__item--incoming {
  align-items: flex-start; }
  .ng2-c-message__item--incoming .ng2-c-message__author {
    display: inline-block; }
  .ng2-c-message__item--incoming .ng2-c-message__message {
    background-color: #f5f5f5; }
  .ng2-c-message__item--incoming .ng2-c-message__summary {
    align-items: flex-start;
    margin-left: 40px; }
    @media (max-width: 767px) {
      .ng2-c-message__item--incoming .ng2-c-message__summary {
        margin-left: 0; } }
  .ng2-c-message__item--incoming .ng2-c-message__avatar {
    bottom: 53px;
    display: block;
    height: 30px;
    left: 19px;
    position: absolute;
    width: 30px; }
    @media (max-width: 767px) {
      .ng2-c-message__item--incoming .ng2-c-message__avatar {
        display: none; } }

.ng2-c-messaging-contact {
  align-items: center;
  display: flex;
  overflow: hidden; }

.ng2-c-messaging-contact__image {
  height: 50px;
  width: 50px; }

.ng2-c-messaging-contact__text {
  color: #28648a;
  font-size: 18px;
  font-weight: 400;
  margin-left: 5px; }

.ng2-c-messaging-contact__icon {
  color: #9b9b9b;
  font-size: 10px;
  margin: 0;
  padding: 0 10px; }

.ng2-c-messaging-contact--small {
  background-color: #f5f5f5;
  border-radius: 4px;
  display: inline-flex;
  margin: 5px;
  margin-left: 0; }
  .ng2-c-messaging-contact--small .ng2-c-messaging-contact__text {
    font-size: 16px; }
  .ng2-c-messaging-contact--small .ng2-c-messaging-contact__image {
    height: 35px;
    width: 35px; }
